@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:0;margin:0;font-family:"Montserrat", sans-serif}body{background-color:black}svg{position:absolute;height:100vh;width:100vw;overflow:visible}.grid-container{display:flex;width:100vw;height:100vh}.swiss-circle-container{position:relative;margin-left:-2em;margin-top:-1em}.swiss-circle-container #circ-1{position:relative;stroke-width:2px}.swiss-circle-container #circ-2{position:relative;stroke-width:4px}.swiss-circle-container #circ-3{position:relative;stroke-width:8px}.swiss-circle-container #circ-4{position:relative;stroke-width:16px}.swiss-circle-container #circ-5{position:relative;stroke-width:32px}.swiss-circle-container #circ-6{position:relative;stroke-width:320px}#title-container{position:absolute;width:auto;left:50%;top:50%;transform:translate(-56%, -50%);margin:0}#name{color:white;font-size:3.8em;font-weight:600}#sub-title{color:white;font-weight:400;font-size:2em;position:relative}@media screen and (max-width: 768px){#title-container{transform:translate(-56%, -50%)}#title-container #name{width:auto;font-size:3.42em}}@media screen and (max-width: 500px){#title-container{transform:translate(-56%, -50%)}#title-container #name{width:auto;font-size:3em}}

